// import http from './../../http'
const actions = {
    authInfo({ commit }, authInfo) {
        commit('setAuthInfo', authInfo)
    },
    loading({ commit }, loading) {
        commit('setLoading', loading)
    },
    destroy({ commit }) {
        commit('destroy')
    },
    isShow({ commit }, isShow) {
        commit('isShow', isShow)
    }
}

export default actions