<template>
   <div data-theme="colored" data-layout="fluid" data-sidebar-position="left" data-sidebar-layout="default">
        <main class="d-flex w-100 h-100">
            <div class="container d-flex flex-column">
                <div class="row vh-100">
                    <div class="col-sm-10 col-md-8 col-lg-5 mx-auto d-table h-100">
                        <div class="d-table-cell align-middle">
                            <div class="text-center mt-4">
                                <h1 class="h2">Welcome back, Batman</h1>
                                <p class="lead">
                                    Sign in to your account to continue
                                </p>
                            </div>
                            <div class="card">
                                <div class="card-body">
                                    <div class="m-sm-4">
                                        <form>
                                            <div class="mb-3">
                                                <label class="form-label">Email</label>
                                                <input class="form-control form-control-lg" v-model="loginData.username" type="text" name="email" placeholder="Enter your email">
                                            </div>
                                            <div class="mb-3">
                                                <label class="form-label">Password</label>
                                                <input class="form-control form-control-lg" v-model="loginData.password" type="password" name="password" placeholder="Enter your password">
                                            </div>
                                            <div class="text-center mt-3">
                                                <a class="btn btn-lg btn-primary login" :class="{'disable-btn' : !disableBtn}" @click="submit()">Sign in</a>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
    import { mapActions, mapGetters } from 'vuex'
    import { login } from './../../api/auth'

    export default {
        components: {},
        name: 'Login',
        metaInfo: {
            title: 'Login',
        },
        data() {
            return {
                loginData: {
                    username: "",
                    password: "",
                    device_id: ""
                },
            }
        },
        computed: {
            disableBtn() {
                return this.loginData.username != "" && this.loginData.password != ""
            }
        },
        methods: {
            async submit() {
                try {
                    this.loading(true)
                    let data = await login(this.loginData)
                    this.loading(false)
                    this.authInfo(data.data)
                    this.$notify({
                        title: 'Thành công',
                        message: "Đăng nhập thành công.",
                        type: 'success'
                    });
                    this.$router.push("/")
                    .catch((e)=>{
                        console.log(e)
                    });
                } catch (error) {
                    this.loading(false)
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
                
            },
            detectBrowser() {
                this.loginData.device_id = navigator.appCodeName + navigator.appName + navigator.appVersion + navigator.appVersion + navigator.cookieEnabled + navigator.platform + navigator.userAgent;
            },
            ...mapActions(['loading']),
            ...mapActions(['authInfo']),
            ...mapGetters( ['auth_info'])
        },
        mounted() {
            this.detectBrowser()
            if (this.auth_info().token) {
                this.$router.push("/")
                .catch((e)=>{
                    console.log(e)
                });
            }
        }
    }
</script>