<template>
    <div id="app" v-loading="loading">
        <div data-theme="colored" data-layout="fluid" data-sidebar-position="left" data-sidebar-layout="default" :is="this.$route.meta.layout || 'div'">
            <div id="app" v-loading="loading">
                <div class="wrapper">
                    <Menu></Menu>
                    <div class="main">
                        <Header></Header>
                        <main class="content">
                            <router-view></router-view>
                        </main>
                        <Footer></Footer>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import router from './router'
    import Menu from './pages/layout/Menu.vue'
    import Header from './pages/layout/Header.vue'
    import Footer from './pages/layout/Footer.vue'

    export default {
        name: 'App',
        metaInfo: {
            title: '1989s Media',
            titleTemplate: '%s | Tăng tương tác Facebook tại mualikegiare.xyz'
        },
        components: {
            Menu,
            Header,
            Footer,
        },
        data() {
            return {
                title: '',
            }
        },
        computed: {
            ...mapGetters(['loading']),
            ...mapGetters(['is_show'])
        },
        methods: {
            ...mapGetters(['auth_info'])
        },
        mounted() {
            if (!this.auth_info().token) {
                router.push({ name: "Login"})   
            }
        }
    }
</script>