import axios from 'axios'
import store  from './store';


const http = axios.create({
    baseURL: process.env.VUE_APP_URL_ADMIN_API
});

const httpAdmin = axios.create({
    baseURL: process.env.VUE_APP_URL_USER_API
});


http.interceptors.request.use(
    config => {
        // do something before request is sent
        let token = store.state.auth.authInfo.token
        if (token) {
            config.headers['token'] = token
        }
        return config
    },
    error => {
        // do something with request error
        console.log(error) // for debug
        return Promise.reject(error)
    }
)

// response interceptor
http.interceptors.response.use(
    response => {
        const res = response.data

        return res
    },
    error => {
        console.log('aaaaaaaaa')
        console.log(error)
        if(error.response.data) {
            console.log(error.response.data)
            if (error.response.data.code === 401) {
                localStorage.setItem("authInfo", JSON.stringify([]));
                return window.location = "login"

            }

            if (error.response.code < 200 || error.response.code >= 300) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.message,
                });
            }
        }
        
        return Promise.reject(error)
    }
)

httpAdmin.interceptors.request.use(
    config => {
        // do something before request is sent
        let token = store.state.auth.authInfo.token
        if (token) {
            config.headers['token'] = token
        }
        return config
    },
    error => {
        // do something with request error
        console.log(error) // for debug
        return Promise.reject(error)
    }
)

// response interceptor
httpAdmin.interceptors.response.use(
    response => {
        const res = response.data

        return res
    },
    error => {
        console.log(error)
        if(error.response) {
            console.log(error.response)
            if (error.response.code === 401) {
                return window.location = "/"

            }

            if (error.response.code < 200 || error.response.code >= 300) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.message,
                });
            }
        }
        
        return Promise.reject(error)
    }
)

export { http, httpAdmin };