<template>
    <div class="container-fluid p-0">
        <h1 class="h3 mb-3">Chi tiết người dùng</h1>
        <div class="row">
            <div class="col-12 col-xl-12">
                <div class="card ">
                    <div class="card-body">
                        <form>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Họ và tên</label>
                                <div class="col-sm-9">
                                    <input type="text" class="el-input__inner" v-model="user.fullname" placeholder="Họ và tên">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Số điện thoại</label>
                                <div class="col-sm-9">
                                    <input type="text" class="el-input__inner" :disabled="true" v-model="user.username" placeholder="Số điện thoại">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Token</label>
                                <div class="col-sm-9">
                                    <input type="text" class="el-input__inner" :disabled="true" v-model="user.token" placeholder="Token">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Số dư</label>
                                <div class="col-sm-9">
                                    <input type="number" class="el-input__inner" v-model="user.balance" placeholder="Số dư">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Tên ngân hàng</label>
                                <div class="col-sm-9">
                                    <input type="text" class="el-input__inner" v-model="user.BankName" placeholder="Tên ngân hàng">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Chủ tài khoản</label>
                                <div class="col-sm-9">
                                    <input type="text" class="el-input__inner" v-model="user.AccountName" placeholder="Chủ tài khoản">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Số tài khoản</label>
                                <div class="col-sm-9">
                                    <input type="number" class="el-input__inner" v-model="user.BankNumber" placeholder="Số tài khoản">
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <label class="col-form-label col-sm-3 text-sm-start">Trạng thái</label>
                                <div class="col-sm-9">
                                    <el-select v-model="user.status" placeholder="Select" class="w-100">
                                        <el-option
                                            v-for="(item, key) in options"
                                            :key="key"
                                            :label="item"
                                            :value="item">
                                        </el-option>
                                    </el-select>
                                </div>
                            </div>
                            <div class="mb-3 row">
                                <div class="col-sm-12 d-flex justify-content-end">
                                    <el-button type="primary" @click="updateUser">Cập nhật</el-button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { detailUser, updateUser } from './../../api/user'
    export default {
        name: "UserDetail",
        metaInfo: {
            title: 'Chi tiết người dùng',
        },
        data() {
            return {
                id: '',
                user: '',
                options: ["Active", "Pause"]
            }
        },
        methods: {
            async userInfo() {
                try {
                    this.$store.state.auth.loading = true
                    let user = await detailUser(this.id)
                    this.user = user.data
                    this.$store.state.auth.loading = false
                } catch (error) {
                    this.$store.state.auth.loading = false
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            },
            async updateUser() {
                try {
                    this.user.balance = parseFloat(this.user.balance)
                    this.$store.state.auth.loading = true
                    await updateUser(this.user)
                    this.$store.state.auth.loading = false
                    this.$notify({
                        title: 'Thành công',
                        message: "Cập nhật thành công",
                        type: 'success'
                    });
                } catch (error) {
                    this.$store.state.auth.loading = false
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            }
        },
        mounted() {
            this.id = this.$route.query.id
            this.userInfo()
        }
    }       
</script>