import {http} from './../http'

export function getAllUser(offset, limit) {
    let data = {
        offset: offset ?? "",
        limit: limit ?? 10,
        status: ["Active", "Pause"]
    }

    return http.post('users/all', data)
}

export function detailUser(user_id) {
    return http.get('users/detail?user_id=' + user_id)
}

export function updateUser(params) {
    let data = {
        user_id: params.id,
        fullname: params.fullname,
        status: params.status,
        bank_name: params.BankName,
        bank_number: params.BankNumber,
        account_name: params.AccountName,
        balance: params.balance,
    }

    return http.post('users/update', data)
}

export function deleteUserByUserId(user_id) {
    let data = {
        user_id: user_id
    }

    return http.post('users/delete', data)
}
