const mutations = {
    setAuthInfo(state, authInfo) {
        state.authInfo = authInfo
        localStorage.setItem("authInfo", JSON.stringify(state.authInfo))
    },
    setLoading(state, loading) {
        state.loading = loading
    },
    destroy(state) {
        state.authInfo = []
        localStorage.setItem("authInfo", JSON.stringify(state.authInfo));
    },
    isShow(state, isShow) {
        state.isShow = isShow
    }
}

export default mutations