<template>
    <div class="container-fluid p-0">
        <h1 class="h3 mb-3">Danh sách người dùng</h1>
        <div class="row">
            <div class="col-12 col-xl-12">
                <div class="card ">
                    <div class="table-responsive">
                        <table class="table">
                            <thead>
                                <tr>
                                    <th>STT</th>
                                    <th>Name</th>
                                    <th>Số điện thoại</th>
                                    <th>Token</th>
                                    <th>Số dư</th>
                                    <th>Trạng thái</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(user, index) in listUsers" :key="index">
                                    <td>{{index + 1}}</td>
                                    <td>{{user.fullname}}</td>
                                    <td>{{user.username}}</td>
                                    <td>{{user.token}}</td>
                                    <td>{{new Intl.NumberFormat().format(user.balance)}}đ</td>
                                    <td><span class="badge" :class="user.status == 'Active' ? 'bg-success' : 'bg-warning'">
                                        {{user.status == 'Active' ? 'Hoạt động' : 'Tạm dừng'}}
                                    </span></td>
                                    <td class="table-action">
                                        <el-button size="mini" @click="editUser(user)">Edit</el-button>
                                        <el-button size="mini" type="danger" @click="deleteUser(user)">Delete</el-button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <scroll-loader 
                            :loader-method="users" 
                            :loader-size="20" 
                            :loader-disable="completedLoadmore">
                        </scroll-loader>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { getAllUser, deleteUserByUserId } from './../../api/user'
    import { mapActions } from 'vuex'
    import router from './../../router'
    export default {
        name: "ListUser",
        metaInfo: {
            title: 'Quản lý người dùng',
        },
        data() {
            return {
                offset: '',
                limit: 10,
                listUsers: [],
                completedLoadmore: false,
            }
        },
        methods: {
            async users() {
                try {
                    this.loading(true)
                    let listUsers = await getAllUser(this.offset, this.limit)
                    this.loading(false)
                    if (listUsers.data.total <= 0) {
                        this.completedLoadmore = true;
                    } else {
                        listUsers.data.data.forEach(user => {
                            this.listUsers.push(user)
                        });
                        this.offset = listUsers.data.last_offset
                    }
                } catch (error) {
                    this.loading(false)
                    this.completedLoadmore = true;
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            },
            async deleteUser(user) {
                try {
                    this.loading(true)
                    await deleteUserByUserId(user.id)
                    var index = this.listUsers.indexOf(user);
                    this.listUsers.splice(index, 1);
                    this.loading(false)
                    this.$notify({
                        title: 'Thành công',
                        message: "Xóa thành công",
                        type: 'success'
                    });
                } catch (error) {
                    this.loading(false)
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            },
            editUser(user) {
                // routes: [{ path: '/a', redirect: '/b' }]
                router.push({ name: 'user.detail', query: { id: user.id } })
            },
            ...mapActions(['loading']),
        }
    }
</script>